.custom_container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  color: #dcdcdc;
  background-color: transparent;
}

.custom_container_header {
  text-align: center;
  margin-bottom: 40px;
}

.custom_container_title {
  font-size: 2.5rem;
  color: #ffcc00;
  text-shadow: 0px 0px 10px rgba(255, 204, 0, 0.7);
  margin-bottom: 20px;
}

.video_section {
  height: auto;
  width: 70%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  margin-bottom: 30px;
  border: 2px solid gray;
}

.video_section iframe {
  width: 100%;
  height: auto;
  aspect-ratio: 16 / 9;
}

.text_section {
  margin: 50px 0;
}

.text_section h2 {
  font-size: 1.6rem;
  color: #00ffcc;
  text-shadow: 0px 0px 6px rgba(0, 255, 204, 0.8);
  margin-bottom: 15px;
}

.text_section h3 {
  color: #e74a7e;
  margin-bottom: 15px;
}

.text_section p {
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 25px;
  color: #dcdcdc;
}

.image_section {
  text-align: center;
  margin: 30px 0;
}

.responsive_image {
  width: 100%;
  max-width: 700px;
  border-radius: 10px;
  box-shadow: 0px 0px 15px rgba(255, 255, 255, 0.2);
}

.summary_section {
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  box-shadow: 0px 0px 20px rgba(255, 255, 255, 0.2);
}

.summary_section h2 {
  font-size: 1.8rem;
  color: #ff4500;
  text-shadow: 0px 0px 8px rgba(255, 69, 0, 0.8);
  margin-bottom: 20px;
}

.summary_section p {
  font-size: 1.2rem;
  line-height: 1.7;
  color: #dcdcdc;
  font-style: italic;
}

@media screen and (max-width: 992px) {
  .video_section {
    height: auto;
    width: 100%;

  }
}